  font-family: DM Sans, sans-serif;
}

img {
  display: block;
}

section {
  margin-bottom: 50px;
  overflow: hidden;
}

.container {
  max-width: 480px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

